<template>
  <section class="hero is-fullheight">
    <div class="hero-head">
      <nav class="navbar">
        <div class="container">
          <div class="navbar-brand">
            <a class="navbar-item" href="../">
              <img src="@/assets/logo.png" alt="Logo">
            </a>
            <span class="navbar-burger burger" data-target="navbarMenu">
              <span></span>
              <span></span>
              <span></span>
            </span>
          </div>
          <div id="navbarMenu" class="navbar-menu">
            <div class="navbar-start">
                        <span class="navbar-item">
                          <router-link class="button is-white is-small" to="panel"><span class="icon">
                                   <i class="fa fa-flash"></i>
                               </span>
                               <span>Cromos + buscados</span>
                          </router-link>
                       </span>
            </div>
          </div>
        </div>
      </nav>
    </div>

    <div class="hero-body">
      <div class="container has-text-left">
        <div class="columns">
          <div class="column is-6">
            <h1 class="title is-size-1">
              Album Rusia 2018
            </h1>
            <h2 class="subtitle is-info">
              Intercambia cromos con personas que se encuentre cerca a ti.
            </h2>
            <div class="field">
              <p class="control">
                <button class="button is-link" @click="openFbLoginDialog" type="button"><span class="icon"><i class="fa fa-facebook"></i></span><span>Iniciar Sesion</span></button>
              </p>
            </div>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
export default {
  name: 'Login',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  },
  methods: {
    openFbLoginDialog () {
      FB.login(function(response) {
        console.log(response);
      }, { scope: 'email' })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .hero{
    background: linear-gradient(rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.2)), url(/assets/album2018/img/cover.jpg) no-repeat center center fixed;
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    background-size: cover;
  }
</style>



